import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import {
  getStudyClassificationsIn10,
  deleteStudyClassificationsIn10,
} from 'services/studyClassificationIn10';

class PointLadderTenPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
    };
  }

  handleDelete = async id => {
    await deleteStudyClassificationsIn10({ id });
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'M xp loi',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Xp loi',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tn ting anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'T im',
        dataIndex: 'fromGrade',
        key: 'fromGrade',
      },
      {
        title: 'n im',
        dataIndex: 'toGrade',
        key: 'toGrade',
      },
      {
        title: 'Tn h',
        dataIndex: 'trainType',
        key: 'trainType',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#/point-ladder-ten/form/edit?id=${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card>
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#/point-ladder-ten/form/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getStudyClassificationsIn10}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <PointLadderTenPage />
  </Page>
);



